import { usersAPI } from "../Components/Api/Api";
import {updateObjectInArray} from "../Utils/Helpers/objectHelpers";

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: []
}

const UsersReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'FOLLOW':
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", {following: true})
            }
        case 'UNFOLLOW':
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", {following: false})
            }
        case 'SET_USERS': {
            return {
                ...state,
                users: action.users
            }
        }
        case 'SET_CURRENT_PAGE': {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }
        case 'SET_TOTAL_USERS_COUNT': {
            return {
                ...state,
                totalUsersCount: action.totalUsersCount
            }
        }
        case 'TOGGLE_IS_FETCHING': {
            return {
                ...state,
                isFetching: action.isFetching
            }
        }
        case 'TOGGLE_IS_FOLLOWING_PROGRESS': {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }
        }
        default:
            return state;
    }
}

export const followSuccess = (userId) => {
    return { type: 'FOLLOW', userId };
}

export const unfollowSuccess = (userId) => {
    return { type: 'UNFOLLOW', userId };
}

export const setUsers = (users) => {
    return { type: 'SET_USERS', users };
}

export const setCurrentPage = (currentPage) => {
    return { type: 'SET_CURRENT_PAGE', currentPage };
}

export const setTotalUsersCount = (totalUsersCount) => {
    return { type: 'SET_TOTAL_USERS_COUNT', totalUsersCount };
}

export const toggleIsFetching = (isFetching) => {
    return { type: 'TOGGLE_IS_FETCHING', isFetching };
}
export const toggleFollowingProgress = (isFetching, userId) => {
    return { type: 'TOGGLE_IS_FOLLOWING_PROGRESS', isFetching, userId };
}

export const requestUsers = (currentPage, pageSize) => async (dispatch) => {
    dispatch(toggleIsFetching(true));
    dispatch(setCurrentPage(currentPage));

    let data = await usersAPI.getUsers(currentPage, pageSize);

    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
}

const followUnfollowFunc = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId));

    let response = await apiMethod(userId);

    if (response.data.resultCode == 0) {
        dispatch(actionCreator(userId));
    }

    dispatch(toggleFollowingProgress(false, userId));
}

export const follow = (userId) => async (dispatch) => {
    followUnfollowFunc(dispatch, userId, usersAPI.follow.bind(userId), followSuccess);
}

export const unfollow = (userId) => async (dispatch) => {
    followUnfollowFunc(dispatch, userId, usersAPI.unfollow.bind(userId), unfollowSuccess);
}

export default UsersReducer;